import {useState} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {WordActions} from "../actions/word";
import {SET_LOADING} from "../constants/action-types";

const useWord = (window) => {
        const dispatch = useDispatch();

        const targetWord = useSelector(state => state.word.targetWord);
        const targetWordDefinition = useSelector(state => state.word.targetWordDefinition);
        const guessedWord = useSelector(state => state.word.guessedWord);
        const previousGuessedWords = useSelector(state => state.word.previousGuessedWords);
        const length = useSelector(state => state.word.length);
        const correctAnswer = useSelector(state => state.word.correctAnswer);
        const guessedLetters = useSelector(state => state.word.guessedLetters);

        const [errors, setErrors] = useState({});

        const setGuessedLetter = (letter, index) => {
            dispatch({type: SET_LOADING, loading: true});
            return new Promise(async (resolve, reject) => {
                dispatch(WordActions.setGuessedLetter(letter, index)).then(resp => {
                    setErrors({});
                    dispatch({type: SET_LOADING, loading: false});
                    window.scrollTo(0, 0)
                    resolve(resp)
                }, (error) => {
                    setErrors(error);
                    dispatch({type: SET_LOADING, loading: false});
                    resolve(false);
                });
            });
        };

        const submitGuessedWord = (letter, index) => {
            dispatch({type: SET_LOADING, loading: true});
            return new Promise(async (resolve, reject) => {
                dispatch(WordActions.submitGuessedWord(letter, index, guessedWord, targetWord)).then(resp => {
                    setErrors({});
                    dispatch({type: SET_LOADING, loading: false});
                    window.scrollTo(0, 0)
                    resolve(resp)
                }, (error) => {
                    setErrors(error);
                    dispatch({type: SET_LOADING, loading: false});
                    resolve(false);
                });
            });
        };

        const onClickKeyboardLetter = (letter) => {
            return new Promise(async (resolve, reject) => {
                if(guessedWord.replace(/ /g,'').length<targetWord.length) {
                    dispatch(WordActions.onClickKeyboardLetter(letter)).then(resp => {
                        resolve(resp)
                    }, (error) => {
                        resolve(false);
                    });
                }else{
                    resolve(false);
                }
            });
        };

        const onClickKeyboardBackspace = (letter) => {
            return new Promise(async (resolve, reject) => {
                // if(guessedWord.replace(/ /g,'').length<targetWord.length) {
                    dispatch(WordActions.onClickKeyboardBackspace(letter)).then(resp => {
                        resolve(resp)
                    }, (error) => {
                        resolve(false);
                    });
                // }else{
                //     resolve(false);
                // }
            });
        };

        const onClickKeyboardEnter = () => {
            return new Promise(async (resolve, reject) => {
                if(guessedWord.replace(/ /g,'').length===targetWord.length) {
                    dispatch(WordActions.onClickKeyboardEnter(guessedWord,targetWord)).then(resp => {
                        resolve(resp)
                    }, (error) => {
                        resolve(false);
                    });
                }else{
                    resolve(false);
                }
            });
        };

        const getTargetWord = (length) => {
            dispatch({type: SET_LOADING, loading: true});
            return new Promise(async (resolve, reject) => {
                dispatch(WordActions.getTargetWord(length)).then(resp => {
                    
                    setErrors({});
                    dispatch({type: SET_LOADING, loading: false});
                    window.scrollTo(0, 0)
                    resolve(resp)
                }, (error) => {
                    setErrors(error);
                    dispatch({type: SET_LOADING, loading: false});
                    resolve(false);
                });
            });
        };


        const setScore = () => {
            dispatch({type: SET_LOADING, loading: true});
            return new Promise(async (resolve, reject) => {
                dispatch(WordActions.setScore(length)).then(resp => {
                    
                    setErrors({});
                    dispatch({type: SET_LOADING, loading: false});
                    window.scrollTo(0, 0)
                    resolve(resp)
                }, (error) => {
                    setErrors(error);
                    dispatch({type: SET_LOADING, loading: false});
                    resolve(false);
                });
            });
        };

        const getNewTargetWord = () => {
            dispatch({type: SET_LOADING, loading: true});
            return new Promise(async (resolve, reject) => {
                // dispatch(WordActions.getNewTargetWord(targetWord.length + 1)).then(resp => {
                dispatch(WordActions.getNewTargetWord(targetWord.length)).then(resp => {
                    
                    setErrors({});
                    dispatch({type: SET_LOADING, loading: false});
                    window.scrollTo(0, 0)
                    resolve(resp)
                }, (error) => {
                    setErrors(error);
                    dispatch({type: SET_LOADING, loading: false});
                    resolve(false);
                });
            });
        };

        const checkWord = (word) => {
            dispatch({type: SET_LOADING, loading: true});
            return new Promise(async (resolve, reject) => {
                dispatch(WordActions.checkWord(word)).then(resp => {
                    
                    setErrors({});
                    dispatch({type: SET_LOADING, loading: false});
                    window.scrollTo(0, 0)
                    resolve(resp)
                }, (error) => {
                    setErrors(error);
                    dispatch({type: SET_LOADING, loading: false});
                    resolve(false);
                });
            });
        };

        return {
            errors,
            setErrors,
            targetWord,
            targetWordDefinition,
            guessedWord,
            previousGuessedWords,
            correctAnswer,
            guessedLetters,
            setGuessedLetter,
            submitGuessedWord,
            setScore,
            getTargetWord,
            getNewTargetWord,
            onClickKeyboardLetter,
            onClickKeyboardBackspace,
            onClickKeyboardEnter
        };
    }
;

export default useWord;
