import {toast} from "react-toastify";
import CheckCircleOutlineOutlinedIcon from '@mui/icons-material/CheckCircleOutlineOutlined';
import WarningAmberOutlinedIcon from '@mui/icons-material/WarningAmberOutlined';
import VpnKeyOutlinedIcon from '@mui/icons-material/VpnKeyOutlined';
import Colours from "../styles/colours";
import React from "react";

let isAnimating = false;

let closeDelay = 3000;

let options = {
    position: toast.POSITION.TOP_CENTER,
    // autoClose: false,
    autoClose: closeDelay,
    hideProgressBar: true,
    closeOnClick: true,
    pauseOnHover: false,
    draggable: false,
    progress: undefined
}

let style = {
    borderRadius: 100,
    // width: 500,
    textAlign: 'center',
    fontFamily: 'Montserrat'
}

function displayError(string, auth, linger) {
    toast.error(string, {
        position: "top-center",
        autoClose: closeDelay,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
    });
}

function displaySuccess(string, auth, linger) {
    toast.success(string, {
        position: "top-center",
        autoClose: closeDelay,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
    });
}

function displayQuickAlert(string, auth, linger) {
    toast.success(string, {
        position: "top-center",
        autoClose: 2500,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
    });
}

async function asyncDisplaySuccess(string, auth) {
    return new Promise((resolve, reject) => {
        if (!isAnimating) {
            if (string) {
                toast.success(
                    (<div className={'row justify-content-center align-items-center align-content-center'}>
                        {!auth &&
                        <CheckCircleOutlineOutlinedIcon style={{marginRight: 10}}/>
                        }
                        {auth &&
                        <VpnKeyOutlinedIcon style={{marginRight: 10, fill: Colours.white}}/>
                        }
                        <span>{`${string}`}</span>
                    </div>),
                    {
                        ...options,
                        onOpen: () => {
                            isAnimating = true;
                        },
                        onClose: () => {
                            isAnimating = false;
                            return resolve(true)
                        },
                        style: {
                            ...style,
                            backgroundColor: Colours.success,
                            color: Colours.green,
                        }
                    });
            }
        }
    })
}

async function asyncDisplayError(string, auth) {
    return new Promise((resolve, reject) => {
        if (!isAnimating) {
            if (string) {
                toast.success(
                    (<div className={'row justify-content-center align-items-center align-content-center'}>
                        {!auth &&
                        <WarningAmberOutlinedIcon style={{marginRight: 10, fill: Colours.white}}/>
                        }
                        {auth &&
                        <VpnKeyOutlinedIcon style={{marginRight: 10, fill: Colours.white}}/>
                        }
                        <span>{string}</span>
                    </div>),
                    {
                        ...options,
                        onOpen: () => {
                            isAnimating = true;
                        },
                        onClose: () => {
                            isAnimating = false;
                            return resolve(true)
                        },
                        style: {
                            ...style,
                            backgroundColor: Colours.red,
                            color: Colours.white,
                        }
                    });
            }
        }
    })
}

async function asyncDisplayErrorRedirect(string, auth) {
    return new Promise((resolve, reject) => {
        if (!isAnimating) {
            if (string) {
                toast.success(
                    (<div className={'row justify-content-center align-items-center align-content-center'}>
                        {!auth &&
                        <WarningAmberOutlinedIcon style={{marginRight: 10, fill: Colours.white}}/>
                        }
                        {auth &&
                        <VpnKeyOutlinedIcon style={{marginRight: 10, fill: Colours.white}}/>
                        }
                        <span>{string}</span>
                    </div>),
                    {
                        ...options,
                        autoClose:false,
                        onOpen: () => {
                            isAnimating = true;
                        },
                        onClose: () => {
                            isAnimating = false;
                        },
                        onClick:() => {
                            return resolve(true)
                        },
                        style: {
                            ...style,
                            backgroundColor: Colours.red,
                            color: Colours.white,
                        }
                    });
            }
        }
    })
}

export {displayError, displaySuccess, displayQuickAlert, asyncDisplayError, asyncDisplaySuccess, asyncDisplayErrorRedirect}
